  position: relative;
  background: linear-gradient(91.45deg, #005b8b -3.45%, #61c8ff 52.13%, #005b8b 103.2%);
  overflow: hidden;
  transition: 0.4s ease;
}
.cta-btn-blue::after {
  content: "подробнее";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(-50%, -50%);
  background: linear-gradient(91.45deg, #005b8b -3.45%, #278ec5 52.13%, #005b8b 103.2%);
  opacity: 1;
  pointer-events: none;
  transition: 0.4s ease;
  border-radius: 10px;
}
.cta-btn-blue:hover {
  overflow: visible;
}
